}

.simple-bar-chart {
  height: 10px;
}

.simple-bar-chart-label {
  float: left;
}

.simple-bar-chart-value {
  float: right;
}

.good-bad-ugly {
  background: linear-gradient(to right, rgb(0, 200, 0), rgb(255, 255, 0), rgb(200, 0, 0));
  height: 20px;
}

/**
 * components/tables.css
 *
 * Project Tables.
 *
 */

table.vuetable {
  width: 100%;
  table-layout: fixed;
  overflow: hidden;
}

.retour-menubtn-asc::after {
  content: 'downangle';
}

.retour-menubtn-desc::after {
  content: 'upangle';
}

/**
 * Dashboard chart
 */

table.retour-dashboard th.vuetable-th-redirectSrcUrl {
  width: 34% !important;
}

th.vuetable-th-referrerUrl {
  width: 20% !important;
}

th.vuetable-th-remoteIp {
  width: 14% !important;
}

th.vuetable-th-hitCount {
  width: 8% !important;
  text-align: right !important;
}

th.vuetable-th-hitLastTime {
  width: 18% !important;
}

th.vuetable-th-handledByRetour {
  width: 12% !important;
}

th.vuetable-th-addLink {
  width: 3% !important;
}

/**
 * Redirects chart
 */

table.retour-redirects th.vuetable-th-redirectSrcUrl {
  width: 30% !important;
}

th.vuetable-th-redirectDestUrl {
  width: 24% !important;
}

th.vuetable-th-redirectMatchType {
  width: 13% !important;
}

th.vuetable-th-redirectHttpCode {
  width: 10% !important;
}

th.vuetable-th-deleteLink {
  width: 5% !important;
}

/**
 * Column alignment
 */

td.text-center {
  text-align: center !important;
}

td.text-right {
  text-align: right !important;
}

/**
 * components/tables.css
 *
 * Project Tooltips.
 *
 */

.webperf-tooltip {
  position: relative;
  cursor: default;
}

.webperf-tooltip .webperf-tooltiptext {
  visibility: hidden;
  width: 300px;
  background-color: gray;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  top: 85%;
  left: 50%;
  margin-left: -150px;
}

.webperf-tooltip .webperf-tooltiptext::after {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent grey transparent;
}

.webperf-tooltip:hover .webperf-tooltiptext {
  visibility: visible;
}

.webperf-tooltiptext.webperf-tooltip-visible {
  visibility: visible !important;
  width: 60px;
  margin-left: -30px;
  top: 150%;
}

